import React from "react";

export default function Service(props) {
  return (
    <div
      id={props.id ? props.id : null}
      style={{ display: "flex", gap: "5%", marginBottom: 40 }}
    >
      <div
        style={{ display: "flex", flex: 1, gap: 20, color: "rgb(84, 84, 84)" }}
      >
        <img className="h-[50px] w-[50px]" src={props.icon} height={70} alt="icons" />
        <div>
          <h6
            style={{
              width: "100%",
              paddingBottom: 10,
              fontWeight: 'bold'
            }}
          >
            {props.title}
          </h6>
          <p>{props.description}</p>
        </div>
      </div>
    </div>
  );
}
